@import url("https://fonts.maateen.me/solaiman-lipi/font.css");
$primaryColor: green;
body {
  font-family: "SolaimanLipi", sans-serif;
}

.battery {
  height: 500px;
  width: 150px;
  background: #ddd;
  position: relative;
  border: 10px solid #000;
  &:before {
    content: "";
    width: 50px;
    height: 17px;
    background: #000;
    position: absolute;
    top: -21px;
    left: 50%;
    transform: translateX(-50%);
  }
  .unit {
    width: 100%;
    background: green;
    position: absolute;
    bottom: 0;
    transition: 0.5s all;
    .percent {
      width: 133%;
      height: 5px;
      background-color: #111;
      position: absolute;
      top: 0;

      span {
        position: absolute;
        right: -21px;
        background: #ddd;
        width: 40px;
        text-align: center;
        height: 40px;
        line-height: 40px;
        border-radius: 40px;
        top: -14px;
      }
    }
  }
}

.intro {
  transition: 0.5s all;
  .btn {
    display: flex;
    border-radius: 50px;
    border: 3px solid #ddd;
    padding: 1px 0 2px 29px;
    overflow: hidden;
    position: relative;
    &.btn-submit {
      border: 0px solid;
      padding: 0px;
      &:hover {
        background-color: transparent;
        &:before {
          content: "";
          background-color: transparent;
        }
      }
    }

    &:hover {
      &:before {
        content: "";
        position: absolute;
        background-color: $primaryColor;
        left: 0;
        width: 100%;
        transition: 0.5s all;
        height: 100%;
      }
      color: #fff;
    }
    &:before {
      content: "";
      position: absolute;
      background-color: $primaryColor;
      left: 0;
      width: 0;
      height: 100%;
      transition: 0.5s all;
      z-index: -1;
    }
    img {
      width: 50px;
      height: 50px;
      border-radius: 0;
      margin-right: 4px;
    }
  }
}

// Radio Style

.inputGroup {
  background-color: #fff;
  display: block;
  margin: 10px 0;
  position: relative;

  label {
    background-color: #ddd;
    padding: 12px 30px;
    width: 100%;
    display: block;
    text-align: left;
    color: #3c454c;
    cursor: pointer;
    position: relative;
    z-index: 2;
    transition: color 200ms ease-in;
    overflow: hidden;

    &:before {
      width: 100%;
      height: 10px;
      border-radius: 50%;
      content: "";
      background-color: $primaryColor;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) scale3d(1, 1, 1);
      transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1);
      opacity: 0;
      z-index: -1;
    }

    &:after {
      width: 32px;
      height: 32px;
      content: "";
      border: 2px solid #d1d7dc;
      background-color: #fff;
      background-image: url("data:image/svg+xml,%3Csvg width='32' height='32' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
      background-repeat: no-repeat;
      background-position: 2px 3px;
      border-radius: 50%;
      z-index: 2;
      position: absolute;
      right: 30px;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      transition: all 200ms ease-in;
    }
  }

  input:checked ~ label {
    color: #fff;

    &:before {
      transform: translate(-50%, -50%) scale3d(56, 56, 1);
      opacity: 1;
    }

    &:after {
      background-color: #54e0c7;
      border-color: #54e0c7;
    }
  }

  input {
    width: 32px;
    height: 32px;
    order: 1;
    z-index: 2;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    visibility: hidden;
  }
}
